body.lock {
	overflow: hidden;
}

.modal {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(255, 255, 255, 0.9);
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.2s ease 0s;
	visibility: hidden;
	opacity: 0;
	z-index: 100;
}

.modal.open {
	opacity: 1;
	visibility: visible;
}

.modal__body {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100%;
	transition: all 0.2s ease 0s;
}

.modal__content {
	width: 100%;
	max-width: 320px;
	padding: 36px 30px 46px 30px;
	border: 1px solid #c6c6c6;
	box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
	position: relative;
	background-color: #fff;
	transition: all 0.2s ease 0s;
	transform: translate(0, -100%);
	opacity: 0;
}

.modal-image .modal__content {
	padding: 0;
}

.modal.open .modal__content {
	transform: translate(0, 0);
	opacity: 1;
}

.modal__content_image {
	padding: 0;
}

.modal__image img {
	vertical-align: top;
}

.modal__close {
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 24px;
	color: #000;
	text-decoration: none;
}

@media (min-width: $tablet-width) {  
    .modal__content  {
		max-width: 568px;
		padding: 50px 110px;
	}	
}


